@font-face {
  font-family: "Hind Vadodara";
  src: url(../../../Fonts/HindVadodara-Medium.ttf);
}
.content {
  width: 100%;
  height: 679px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background: url("../../../assets/Desktop/Home/Find/Rectangle 1.png") center/cover no-repeat;
  padding-top: 259px;
  padding-bottom: 137px;
  position: relative;
  isolation: isolate;
}
.content::after {
  content: "";
  position: absolute;
  z-index: -1;
  background-color: rgb(38, 38, 12);
  opacity: 0.73;
  inset: 0;
}
.content .search {
  width: 386px;
  height: 25px;
  display: flex;
  align-items: start;
  justify-content: center;
  border: none;
  border-bottom: 1px solid white;
  opacity: 0.69;
  position: relative;
}
.content .search input {
  width: 100%;
  height: 20px;
  border: none;
  background-color: unset;
  outline: none;
  font-family: "Hind Vadodara";
  font-size: 12px;
  letter-spacing: 0.36px;
  text-align: center;
  padding: 0 40px;
  color: white;
}
.content .search input::-moz-placeholder {
  color: white;
}
.content .search input::placeholder {
  color: white;
}
.content .search .searchSvg {
  width: 12px;
  height: 12px;
  stroke-width: 1.5px;
  stroke: rgb(255, 255, 255);
  position: absolute;
  top: 5px;
  left: 15px;
}/*# sourceMappingURL=Find.module.css.map */