@import url(/src/Fonts/_Fonts.scss);

.cart {
    width: 50%;
    height: auto;
    padding: 10px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    img {
        width: 100%;
        height: 457px;
        border-radius: 20px;
    }
    h3 {
        color: rgb(91, 89, 13);
font-family: Quattrocento;
font-size: 18px;
font-weight: 400;
line-height: 25px;
    }
    p {
        font-family: Hind Vadodara;
font-size: 16px;
font-weight: 400;
line-height: 24px;
    }
    a {
        margin: 20px 0;
        color: rgb(107, 107, 95);
        font-family: Hind Vadodara;
        font-size: 18.31px;
        font-weight: 400;
        line-height: 28.77px;
    }
}

@media screen and (max-width: 700px) {
    .cart {
        width: 100%;
    }
}