@import url(/src/Fonts/_Fonts.scss);

.content {
  width: 100%;
  height: 472px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 180px;
  background: rgb(91, 93, 75);
  margin-bottom: 92px;
  img {
    width: 785px;
    height: 472px;
  }
  .textContent {
    width: 702px;
    height: 280px;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: start;
    h1 {
      color: rgb(255, 255, 255);
      font-family: Quattrocento;
      font-size: 32px;
      margin-bottom: 30px;
    }
    p {
      width: 560px;
      color: rgb(255, 255, 255);
      font-family: Hind Vadodara;
      font-size: 16px;
      margin-bottom: 60px;
    }
    .button {
      width: 100%;
      height: auto;
      display: flex;
      align-items: center;
      justify-content: center;
      a {
        width: 176px;
        height: 44.6px;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1.69px solid rgb(255, 255, 255);
        border-radius: 72.49px;
        color: rgb(255, 255, 255);
        font-family: Hind Vadodara;
        font-size: 14px;
        font-weight: 600;
        text-decoration: none;
      }
    }
  }
}
