@import url("/src/Fonts/_Fonts.scss");
.questions {
  width: 895px;
  cursor: pointer;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: start;
  padding: 20px 40px;
  margin: 20px;
  background-color: #f2f2f0;
  border: 1px solid rgba(113, 110, 35, 0.6509803922);
  border-radius: 10px;
}
.questions h2 {
  color: black;
  font-family: "Hind Vadodara";
  font-size: 18px;
  font-weight: 900;
  line-height: 30px;
}
.questions p {
  margin-top: 15px;
  margin-left: 40px;
  color: black;
  font-family: "Hind Vadodara";
  font-size: 16px;
  font-weight: 400;
  line-height: 30px;
}

@media screen and (max-width: 992px) {
  .questions {
    width: 95%;
  }
}/*# sourceMappingURL=Questions.module.css.map */