.content {
  width: 100%;
  height: 982px;
  background: rgba(203, 204, 190, 0.67);
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 90px;
  padding-bottom: 200px;
  .contactUs {
    width: 700px;
    height: 376px;
    margin-top: 95px;
    display: flex;
    align-items: start;
    justify-content: space-between;
    .socials {
      width: 265px;
      height: 293px;
      margin-right: 144px;
      display: flex;
      align-items: start;
      justify-content: start;
      ul {
        list-style: none;
        li {
          height: 29px;
          margin-bottom: 29px;
          display: flex;
          align-items: center;
          justify-content: start;
          a {
            text-decoration: none;
            color: black;
          }
          svg {
            margin-right: 30px;
          }
        }
      }
    }
    .getInTouch {
      width: 288px;
      height: 100%;
      h2 {
        font-size: 18px;
        margin-bottom: 25px;
      }
      form {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: end;
        justify-content: space-between;
        input,
        textarea {
          border: 0.5px solid #212311;
          border-radius: 7px;
          background-color: unset;
          padding: 9px 11px;
          width: 288px;
          outline: none;
        }
        textarea {
          height: 82px;
          resize: none;
        }
        button {
          border-radius: 10px;
          width: 120px;
          height: 31px;
          background: #5b5d4b;
          outline: none;
          color: white;
          border: none;
        }
      }
    }
  }
}
