@import url("/src/Fonts/_Fonts.scss");
.content {
  width: 100%;
  height: auto;
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.content .cart {
  width: 100%;
  height: 237px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  border-radius: 11.05px;
  box-shadow: 1.66px 1.1px 2.76px 0px rgba(0, 0, 0, 0.19), -1.1px -1.1px 5.52px 0px rgba(0, 0, 0, 0.25);
  background: rgba(86, 89, 60, 0.13);
}
.content .cart h1 {
  color: rgb(33, 35, 17);
  font-family: Hind Vadodara;
  font-size: 18px;
  font-weight: 400;
}
.content .cart p {
  color: rgb(33, 35, 17);
  font-family: Hind Vadodara;
  font-size: 16px;
  font-weight: 400;
  text-align: center;
}
.content .buttons {
  width: 90px;
  height: auto;
  margin-top: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.content .buttons button {
  width: 15px;
  height: 15px;
  background: rgb(203, 204, 190);
  border-radius: 50%;
  border: none;
  outline: none;
  cursor: pointer;
}/*# sourceMappingURL=FeedbackMobileCart.module.css.map */