.cart {
  width: 350px;
  height: 374px;
  margin-bottom: 100px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  img {
    width: 100%;
    height: 317px;
    cursor: pointer;
  }
  .categoryPrice {
    width: 100%;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    h3 {
      font-size: 14px;
      font-weight: 100;
    }
    p {
      font-size: 16px;
      font-weight: 600;
    }
  }
  .nameBtn {
    width: 100%;
    height: 27px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    h2 {
      font-size: 16px;
      font-weight: 600;
    }
    button {
      border: 1.01px solid #d6822a;
      background-color: unset;
      border-radius: 9px;
      width: 116px;
      height: 27px;
      font-size: 10px;
      font-weight: 100;
      cursor: pointer;
    }
    .remove {
      border: 1.01px solid greenyellow;
    }
  }
}

@media screen and (max-width: 992px) {
  .cart {
    margin: 10px;
    img {
      border-radius: 20px;
    }
  }
}
