@import url(/src/Fonts/_Fonts.scss);

.content {
  width: 100%;
  height: 592px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 180px;
  background: #dadbda;
  margin-bottom: 92px;
  img {
    width: 810px;
    height: 592px;
  }
  .textContent {
    width: 670px;
    height: auto;
    h1 {
      color: #c4813b;
      font-family: Qwigley;
      font-size: 40px;
      font-weight: 400;
    }
    h2 {
      color: rgb(79, 85, 34);
      font-family: Quattrocento;
      font-size: 32px;
      font-weight: 400;
      line-height: 40px;
    }
    p {
      width: 653px;
      color: rgb(26, 11, 1);
      font-family: Hind Vadodara;
      font-size: 16px;
      font-weight: 300;
      line-height: 24px;
    }
    .search {
      width: 391px;
      height: 30px;
      margin-top: 73px;
      position: relative;
      input {
        width: 100%;
        height: 100%;
        padding-left: 30px;
        background-color: unset;
        outline: none;
        border: none;
        border-bottom: 1px solid rgb(46, 47, 37);
      }
      svg {
        position: absolute;
        top: 8px;
        left: 8px;
      }
    }
  }
}
