@import url(/src/Fonts/_Fonts.scss);

.content {
  width: 100%;
  height: 519px;
  border-radius: 10px;
  background: url("../../../assets/Desktop/Home/HealthyTime/Rectangle\ 220.png")
      center/cover no-repeat,
    rgb(217, 217, 217);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: end;
  .goToShop {
    width: 100%;
    height: 177px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;

    box-sizing: border-box;
    border-top: 1px solid rgba(255, 255, 255, 0.05);
    border-radius: 20px 20px 10px 10px;

    backdrop-filter: blur(20.7px);
    background: rgba(99, 102, 76, 0.29),
      linear-gradient(
        128.5deg,
        rgba(255, 255, 255, 0.1) 4.827%,
        rgba(199, 199, 199, 0) 89.905%
      ),
      rgba(255, 249, 249, 0);
    h2 {
      color: rgb(255, 255, 255);
      font-family: Hind Vadodara;
      font-size: 20px;
      font-weight: 500;
      line-height: 30px;
    }
    a {
      width: 161px;
      height: 42px;
      display: flex;
      justify-content: center;
      align-items: center;

      text-decoration: none;

      box-sizing: border-box;
      border: 3.55px solid rgb(255, 255, 255);
      border-radius: 20.42px;

      color: rgb(255, 255, 255);
      font-family: Hind Vadodara;
      font-size: 18px;
      font-weight: 600;
      line-height: 27px;
    }
  }
}
