.content {
  width: 1454px;
  height: auto;
  padding-bottom: 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  .cartDiv {
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

@media screen and (max-width: 1860px) {
  .content {
    width: 950px;
    padding: 0;
    .cartDiv {
      height: 900px;
      flex-wrap: wrap;
      align-items: start;
      justify-content: space-around;
    }
  }
}
