.filter {
  width: 1461px;
  height: 59px;
  padding: 0 118px 0 39px;
  border: 1px solid #cbccbe;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 50px;
}
.filter .buttons {
  width: calc(100% - 408px);
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.filter .buttons button {
  border: none;
  background-color: unset;
  cursor: pointer;
  outline: none;
}
.filter .search {
  width: 299px;
  height: 30px;
  background-color: aqua;
  position: relative;
}
.filter .search input {
  width: 100%;
  height: 100%;
  font-size: 16px;
  padding-left: 20px;
  outline: none;
  border: none;
  border-bottom: 1px solid;
}
.filter .search svg {
  position: absolute;
  top: 7px;
  left: 0;
}

@media screen and (max-width: 992px) {
  .filter {
    width: 100%;
    height: auto;
    padding: 0;
    border: none;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 50px;
  }
  .filter .buttons {
    width: 100%;
    height: 80px;
    padding: 0 20px;
  }
  .filter .search {
    width: 100%;
    height: 89px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(93, 92, 30, 0.1);
  }
  .filter .search input {
    width: 240px;
    height: 40px;
    font-size: 16px;
    text-align: center;
    padding: 0;
    background-color: unset;
    color: rgb(116, 114, 0);
    border-bottom: 1px solid rgb(116, 114, 0);
  }
  .filter .search input::-moz-placeholder {
    color: rgb(116, 114, 0);
  }
  .filter .search input::placeholder {
    color: rgb(116, 114, 0);
  }
  .filter .search svg {
    position: absolute;
    top: 40px;
    left: calc(50% - 110px);
  }
  .filter .search svg path {
    stroke: rgb(116, 114, 0);
  }
}/*# sourceMappingURL=DetoxSmoothiesFilter.module.css.map */