@import url(/src/Fonts/_Fonts.scss);

.nav {
  width: 100%;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .pages {
    font-family: "Hind Vadodara";
    font-size: 16px;
    font-weight: 900;
    letter-spacing: 0.8px;
    cursor: pointer;
  }
}

@media screen and (max-width: 1500px) {
  .nav {
    width: 100%;
    .pages {
      font-size: 14px;
    }
  }
}
