@import url(/src/Fonts/_Fonts.scss);

.cart {
  width: 100%;
  height: 150px;
  border-radius: 7.07px;
  box-shadow: 2.83px 2.83px 2.83px 0px rgba(0, 0, 0, 0.25),
    -0.71px -0.71px 2.83px 0px rgba(0, 0, 0, 0.25);
  background: rgb(255, 255, 255);
  padding: 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 15px;
  img {
    width: 200px;
    height: 126px;
    border-radius: 10px;
  }
  .itemInfo {
    width: calc(100% - 215px);
    height: 126px;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: space-between;
    p {
      color: rgb(22, 22, 25);
      font-family: Hind Vadodara;
      font-size: 16px;
      font-weight: 400;
    }
    h2 {
      color: rgb(0, 0, 0);
      font-family: Poppins;
      font-size: 16px;
      font-weight: 900;
    }
  }
}
