.popup {
  width: 585px;
  height: 457px;
  background: rgb(255, 255, 255);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 35px 60px;
}
.popup h1 {
  color: rgb(91, 93, 75);
  font-family: Hind Vadodara;
  font-size: 20px;
  font-weight: 900;
  line-height: 30px;
}
.popup button {
  outline: none;
  cursor: pointer;
  border: none;
  background-color: unset;
  display: flex;
  align-items: center;
  justify-content: center;
}
.popup .goBack {
  border: none;
  background-color: unset;
  width: 100px;
  height: 25px;
  justify-content: space-between;
  font-family: Inter;
  font-size: 16px;
  color: black;
  text-decoration-line: underline;
  cursor: pointer;
}
.popup .goBack svg {
  margin-top: 3px;
}

@media screen and (max-width: 992px) {
  .popup {
    width: 100%;
    height: 100vh;
    padding: 100px 5%;
  }
}/*# sourceMappingURL=SignUp.module.css.map */