@import url("/src/Fonts/_Fonts.scss");
.content {
  width: 100%;
  height: auto;
  padding-bottom: 138px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.content .carusel {
  width: 1755px;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.content .carusel .active,
.content .carusel .cart {
  padding: 20px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 20px;
  background: rgba(86, 89, 60, 0.16);
  box-shadow: 3px 2px 5px 0px rgba(0, 0, 0, 0.19), -2px -2px 10px 0px rgba(0, 0, 0, 0.25);
  text-align: center;
}
.content .carusel .active .stars,
.content .carusel .cart .stars {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.content .carusel .active .stars svg,
.content .carusel .cart .stars svg {
  margin: 5px;
}
.content .carusel .active h1,
.content .carusel .cart h1 {
  color: #212311;
  font-family: "Hind Vadodara";
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  text-transform: capitalize;
}
.content .carusel .active p,
.content .carusel .cart p {
  color: #212311;
  font-family: "Hind Vadodara";
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
}
.content .carusel .active {
  width: 709px;
  height: 248px;
  padding-bottom: 70px;
}
.content .carusel .active p {
  width: 550px;
}
.content .carusel .cart {
  width: 500px;
  height: 175px;
}
.content .buttons {
  width: 100%;
  height: auto;
  padding-top: 56px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.content .buttons .Btn {
  width: 11px;
  height: 11px;
  outline: none;
  border: none;
  border-radius: 50%;
  background-color: #cbccbe;
  margin: 5px;
}

