@font-face {
  font-family: Quattrocento;
  src: url(../../../Fonts/Quattrocento-Regular.ttf);
}
@font-face {
  font-family: "Hind Vadodara";
  src: url(../../../Fonts/HindVadodara-Medium.ttf);
}
.content {
  width: 100%;
  height: 600px;
  background: #5b5d4b;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.content .image {
  width: 1043px;
  height: 100%;
  background: url(../../../assets/Desktop/Home/HealthyTime/Rectangle\ 220.png) center/cover no-repeat;
}
.content .text {
  width: 594px;
  height: 300px;
  padding-left: 85px;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: space-between;
}
.content .text h1 {
  color: #fff;
  font-family: Quattrocento;
  font-size: 32px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-transform: capitalize;
}
.content .text p {
  color: white;
  font-family: "Hind Vadodara";
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
}
.content .text a {
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 170px;
  height: 40px;
  color: white;
  background-color: unset;
  border: 1.686px solid #fff;
  border-radius: 40px;
}

@media screen and (max-width: 1860px) {
  .content .image {
    width: 58%;
  }
  .content .text {
    width: 40%;
    padding-left: 20px;
  }
}/*# sourceMappingURL=HealthyTime.module.css.map */